export const getDesignTokens = (mode) => ({
  palette: {
    mode,
    ...(mode === 'light' && {
      background: {
        default: "#FFF",
      },
      text: {
        primary: "#212121",
      },
    }),
  },
  typography: {
    fontFamily: [
      '"trebuchet ms"',
      'Tahoma',
      'Roboto',
    ].join(','),
  },
});